.app-grid {
  display: grid;
  grid-template-areas:
    "top mode top-right"
    "left grid vegetables"
    "left copyConfig right"
    "left loadConfig right"
    "debug debug debug";
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: auto 60vh auto auto auto;
  padding: 10px;
}

/* Assign each area to its grid area */
.top {
  grid-area: top;
  text-align: center;
}

.mode {
  grid-area: mode;
  text-align: center;
  border-top: 2px solid black;
  border-right: 2px solid black;
  border-left: 2px solid black;
  border-radius: 10px 10px 0px 0px;
  padding-top: 10px;
}

.left {
  grid-area: left;
}

.grid {
  grid-area: grid;
  overflow: hidden;
  width: 80vw;
  height: 60vh;
  border-left: 2px solid black;
  border-bottom: 2px solid black;
  
}

.copyConfig {
  grid-area: copyConfig;
}

.loadConfig {
  grid-area: loadConfig;
}

.right {
  grid-area: right;
}

.debug {
  grid-area: debug;
}

/* Grid container wrapper styling */
.grid-container-wrapper {
  margin-right: 0px !important;
  position: relative;
  overflow: hidden;
  background: #f0f0f0;
  overscroll-behavior: contain;
}

.grid-container {
  position: relative;
  background: #fff;
}

/* Grid cell styling */
.grid-cell {
  width: 100px;
  height: 100px;
  border: 1px solid #aaa;
  box-sizing: border-box;
  float: left;
  position: relative;
}

.grid-cell.selected {
  background-color: lightgreen;
}

/* Container for dropped vegetable images */
.cell-content {
  width: 100%;
  height: 100%;
}

/* Images in cell content */
.cell-content img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* Tooltip styling */
.tooltip {
  /*placeHolder*/
}

/* Selection overlay styling */
.current-selection.add {
  position: absolute;
  border: 2px dashed green;
  background-color: rgba(0, 255, 0, 0.2);
  pointer-events: none;
  z-index: 10;
}

.current-selection.remove {
  position: absolute;
  border: 2px dashed red;
  background-color: rgba(255, 0, 0, 0.2);
  pointer-events: none;
  z-index: 10;
}

/* Mode toggle styling */
.mode-toggle {
  text-align: center;
  margin-bottom: 10px;
}

/* Dropdown container styling */
.dropdown-container {
  font-size: 14px;
  z-index: 1;
}

.dropdown-group {
  margin-bottom: 5px;
}

.dropdown-group-title {
  font-weight: bold;
  margin-bottom: 2px;
  text-align: center;
  text-decoration: underline;  
}

.dropdown-group-title:hover {
  cursor:default;
}

.dropdown-item {
  cursor: pointer;
  padding: 2px 0;
}

.dropdown-item:hover {
  cursor: pointer;
  padding: 2px 0;
  background-color: rgba(0,0,0,0.2);
}
.dropdown-spacer {
  padding-top: 10px;
}


.vegetables {
  grid-area: vegetables;
  border-left: none;
  z-index: 1;
}


/* Vegetable list styling */
.vegetable-list {
  top: 0;
  right: 0;
  z-index: 1;
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 60vh;
  box-shadow: -9px 0px 6px -6px rgba(0,0,0,0.75);
  border-right: 2px solid black;
}

.vegetable-search {
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
  margin-bottom: 5px;
  background: none;
}

.vegetable-list-inner {
  overflow-y: auto;
  flex: 1;
}

.vegetable-item {
  display: flex;
  align-items: center;
  cursor: grab;
  margin-bottom: 5px;
}

.vegetable-item:hover {
  background-color: rgba(0,0,0,0.2);
}

.vegetable-item img {
  width: 50px;
  height: 50px;
  margin-right: 5px;
}

.grid-cell.drag-over {
  border: 30px solid green;
}

.grid-cell.drag-over-selected {
  border: 4px solid green;
}

.grid-cell.drag-over-not-selected {
  background-color: red;
}